import React, {useState} from 'react'
import {Link, Redirect} from 'react-router-dom'
import Base from '../core/Base'
import {signin, authenticate,isAuthenticated} from '../auth/helper/index'


const Signin= ()=>{

    // const  [values, setvalues] = useState({
    //     email: "",
    //     password: "",
    //     error: "",
    //     loading: false,
    //     didRedirect: false
    // })

    // const Signup = () => {
    const [values, setValues] = useState({
        // name: "",
        email: "rsharma@gmail.com",
        password: "12369",
        error: "",
        loading: false,
        didRedirect: false
        // success: false
    });

    const {email, password, error, loading, didRedirect}= values

    const {user} =isAuthenticated()

    const handleChange = name => event => {
        setValues({ ...values, error: false, [name]: event.target.value });
      };

    const onSubmit= event=>{
        event.preventDefault()
        setValues({...values, error:false, loading:true})
        signin({email,password})
        .then(data=>{
            if(data.errors ){
                setValues({...values,error:data.errors, loading:false})
            }
            else if(data.err ){
                setValues({...values,error:data.err, loading:false})
            }
            else{
                authenticate(data, ()=>{
                    // console.log('hey')
                    setValues({
                        ...values,
                        didRedirect:true
                    })
                })
            }

        })
        .catch(console.log('signin request failed'))
        
        
    }

    const performRedirect= ()=>{
        if(didRedirect){
            if(user && user.role==1){
                return (
                    <Redirect to= '/admin/dashboard' /> 
                )
            }
            else{
                return ( <Redirect to= '/user/dashboard' />) 
                     
            }
        }
        // if(isAuthenticated()){
        //     return <Redirect to= '/' />
        // }
    }

    const loadingMessage = () => {
        return (
            loading && (
                <div className='alert alert-info'>
                <h2> Loading..</h2></div>
            )
        )
      };
    
    const errorMessage = () => {
        return (
          <div className="row">
            <div className="col-md-6 offset-sm-3 text-left">
              <div
                className="alert alert-danger"
                style={{ display: error ? "" : "none" }}
              >
                {error}
              </div>
            </div>
          </div>
        );
      };
    

    const signinForm=()=>{
        return(
            <div className='row'>
                <div className='col-md-6 offset-sm-3 text-left'>
                    <form>
                        

                        <div className='form-group'>
                            <label className='text-light'>Email</label>
                            <input onChange={handleChange("email")} className='form-control' 
                            type="email" 
                            value={email}></input>
                        </div>

                        <div className='form-group'>
                            <label className='text-light'>Password</label>
                            <input onChange={handleChange("password")} className='form-control' 
                            type="password"
                            value={password}></input>
                        </div>

                        <button onClick={onSubmit}
                         className='btn btn-success btn-block '>submit</button>
                    </form>
                </div>
            </div>
        )
    }

    return(
        <Base title='Signin page' description='A page where user can sign in the account'>
        {loadingMessage()}
        {errorMessage()}
        {signinForm()}
        {performRedirect()}
        <p className="text-white text-center">{JSON.stringify(values)}</p>
        
        
        
        </Base>
    )
}

export default Signin